import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: lpbUtils } = useWPOptionQuery('lpbUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('lpbPipeChecker', { _wpnonce: lpbUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch();
	}, [lpbUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;